@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-regular.woff2") format("woff2"), url("../fonts/poppins-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorant-garamond-semi-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-semi-bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorant-garamond-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Cormorant Garamond";
  src: url("../fonts/cormorant-garamond-bold-italic.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold-italic.woff") format("woff");
  font-weight: 700;
  font-style: italic;
  font-display: swap;
}

html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0 none;
  clip-path: inset(100%);
}

.main-navigation {
  padding-top: 18px;
  border-top: 1px solid #f6f5f4;
}

.main-navigation__menu {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
  padding-left: 98px;
  padding-right: 98px;
  width: 100%;
  max-width: 1366px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}

.main-navigation__menu li {
  margin-bottom: 10px;
}

.main-navigation__menu li:not(:last-child) {
  margin-right: 33px;
}

.main-navigation__about-company,
.main-navigation__login {
  display: none;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  min-height: 100%;
  text-size-adjust: none;
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
}

.page-body--opened-modal {
  overflow: hidden;
}

.page-body--opened-modal::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 5;
}

.page-header {
  font-family: "Poppins", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
  color: #1d1613;
}

.page-header--nojs {
  background-color: #a87b62;
}

.page-header ul {
  list-style: none;
}

.page-header a:not(.page-header__logo) {
  color: inherit;
}

.page-header a:not(.page-header__logo):visited {
  color: inherit;
}

.page-header a:not(.page-header__logo):hover {
  border-bottom: 1px solid #a87b62;
}

.page-header a:not(.page-header__logo):active {
  border-style: none;
  opacity: 0.5;
}

.page-header__top-container {
  display: flex;
  justify-content: space-between;
  margin: 0 auto 50px;
  padding-left: 98px;
  padding-right: 98px;
  padding-top: 30px;
  max-width: 1366px;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
}

.page-header__button-menu {
  display: none;
}

.page-header__logo {
  position: absolute;
  top: 30px;
  left: 0;
  right: 0;
  margin: auto;
  width: 66px;
  height: 66px;
}

.page-header__logo svg {
  width: 66px;
  height: 66px;
  fill: #a87b62;
}

.page-header__user-menu {
  display: flex;
  flex-wrap: wrap;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 12px;
  padding-left: 0;
  max-width: 45%;
}

.page-header__user-menu li:not(:last-child) {
  margin-right: 35px;
}

.page-header__cart svg {
  display: none;
}

.search input {
  padding-left: 10px;
  font: inherit;
  border-style: none;
  cursor: pointer;
}

.search input::-webkit-input-placeholder {
  color: #9b9997;
}

.search input::-moz-placeholder {
  color: #9b9997;
}

.search input:hover {
  opacity: 0.5;
}

.search input:focus {
  outline: 3px solid #a87b62;
}

.search__button {
  padding: 14px 0 0;
  border-style: none;
  background-color: transparent;
  cursor: pointer;
}

.search__button:hover {
  fill: #a87b62;
}

.search__button:active {
  opacity: 0.5;
}

.search__button svg {
  width: 14px;
  height: 14px;
}

@media (max-width: 1023px) {
  .main-navigation {
    border-style: none;
  }

  .main-navigation a:hover {
    background-color: rgba(0, 0, 0, 0.15);
  }

  .main-navigation--opened {
    position: absolute;
    top: 222px;
    overflow: scroll;
    padding-top: 0;
    padding-bottom: 80px;
    z-index: 10;
  }

  .main-navigation--nojs {
    position: static;
  }

  .main-navigation--closed .main-navigation__menu {
    display: none;
  }

  .main-navigation--opened .main-navigation__menu,
  .main-navigation--nojs .main-navigation__menu {
    display: block;
    margin-bottom: 42px;
    padding: 0;
    font-size: 24px;
    line-height: 36px;
  }

  .main-navigation__menu li {
    margin-bottom: 0;
  }

  .main-navigation__menu li:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .main-navigation--opened .main-navigation__about-company,
  .main-navigation--nojs .main-navigation__about-company {
    display: block;
    margin-top: 0;
    margin-bottom: 46px;
    padding-left: 0;
    font-size: 18px;
    line-height: 23px;
  }

  .main-navigation__about-company li:not(:last-child) {
    margin-bottom: 20px;
  }

  .main-navigation--opened .main-navigation__login,
  .main-navigation--nojs .main-navigation__login {
    display: block;
    font-size: 18px;
    line-height: 23px;
  }

  .page-body--opened-menu {
    background-color: #a87b62;
  }

  .page-header {
    margin: 0 auto;
    padding-left: 45px;
    padding-right: 45px;
    padding-top: 30px;
    width: 100%;
    color: #ffffff;
  }

  .page-header--opened-menu {
    background-color: #a87b62;
  }

  .page-header__top-container {
    display: grid;
    grid-template-columns: max-content 1fr max-content;
    row-gap: 63px;
    margin-bottom: 47px;
    padding-left: 0;
    padding-right: 0;
    padding-top: 25px;
  }

  .page-header--opened-menu .page-header__top-container {
    position: fixed;
    top: 0;
    left: 0;
    padding-left: 45px;
    padding-right: 45px;
    padding-top: 53px;
    width: 100%;
    background-color: #a87b62;
    z-index: 50;
  }

  .page-header--no-js .page-header__top-container {
    position: static;
  }

  .page-header__button-menu {
    display: block;
    padding: 0;
    width: 25px;
    height: 18px;
    border-style: none;
    background-color: transparent;
    cursor: pointer;
  }

  .page-header--nojs .page-header__button-menu {
    display: none;
  }

  .page-header__button-menu:hover svg {
    stroke: #a87b62;
  }

  .page-header__button-menu:active {
    stroke: #1d1613;
    opacity: 0.5;
  }

  .page-header__button-menu svg {
    stroke: #1d1613;
  }

  .page-header--opened-menu .page-header__button-menu:hover svg {
    stroke: #1d1613;
  }

  .page-header--opened-menu .page-header__button-menu svg {
    stroke: #ffffff;
  }

  .page-header__search {
    display: none;
  }

  .page-header--opened-menu .page-header__search,
  .page-header--nojs .page-header__search {
    display: block;
    order: 1;
    grid-column: 1/-1;
  }

  .page-header--opened-menu .page-header__logo {
    top: 30px;
  }

  .page-header--opened-menu .page-header__logo svg {
    fill: #ffffff;
  }

  .page-header--nojs .page-header__logo svg {
    fill: #ffffff;
  }

  .page-header__user-menu {
    grid-column-start: 3;
    padding-top: 0;
  }

  .page-header__login-top {
    display: none;
  }

  .page-header__cart:hover svg {
    stroke: #a87b62;
  }

  .page-header__cart:active {
    opacity: 0.5;
  }

  .page-header__cart svg {
    display: block;
    width: 20px;
    height: 18px;
    fill: none;
    stroke: #1d1613;
  }

  .page-header__cart span {
    display: none;
  }

  .page-header--opened-menu .page-header__cart:hover svg,
  .page-header--nojs .page-header__cart:hover svg {
    stroke: #1d1613;
  }

  .page-header--opened-menu .page-header__cart svg,
  .page-header--nojs .page-header__cart svg {
    stroke: #ffffff;
  }

  .search--opened-menu,
  .search--nojs {
    position: relative;
  }

  .search--opened-menu input,
  .search--nojs input {
    width: 100%;
    height: 40px;
    border-radius: 21px;
    background-color: rgba(0, 0, 0, 0.15);
  }

  .search--opened-menu input::-webkit-input-placeholder,
  .search--nojs input::-webkit-input-placeholder {
    color: transparent;
  }

  .search--opened-menu input::-moz-placeholder,
  .search--nojs input::-moz-placeholder {
    color: transparent;
  }

  .search--opened-menu .search__button,
  .search--nojs .search__button {
    position: absolute;
    bottom: 9px;
    left: 12px;
    fill: #ffffff;
  }

  .search--opened-menu .search__button svg,
  .search--nojs .search__button svg {
    width: 16px;
    height: 16px;
  }
}

@media (max-width: 767px) {
  .main-navigation--opened {
    padding-bottom: 50px;
    top: 179px;
  }

  .main-navigation--opened .main-navigation__menu,
  .main-navigation--nojs .main-navigation__menu {
    margin-bottom: 35px;
  }

  .main-navigation--opened .main-navigation__about-company,
  .main-navigation--nojs .main-navigation__about-company {
    margin-bottom: 33px;
  }

  .page-header {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 13px;
  }

  .page-header__top-container {
    row-gap: 50px;
    margin-bottom: 34px;
  }

  .page-header--opened-menu .page-header__top-container {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 36px;
  }

  .page-header--opened-menu .page-header__button-menu {
    padding-top: 0;
  }

  .page-header__logo {
    top: 20px;
    width: 52px;
    height: 52px;
  }

  .page-header--opened-menu .page-header__logo {
    top: 20px;
  }

  .page-header__logo svg {
    width: 52px;
    height: 52px;
  }
}